import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import AddVersionControlIcon from '@material-ui/icons/PlaylistAdd';
import VersionControlIcon from '@material-ui/icons/FeaturedPlayListRounded';
import AdminIcon from '@material-ui/icons/SupervisedUserCircleRounded';
import TutorialIcon from '@material-ui/icons/LibraryBooksRounded';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import TableIcon from '@material-ui/icons/TableChartRounded';
import SQLIcon from '@material-ui/icons/DescriptionRounded';
import EditTableIcon from '@material-ui/icons/AddBoxRounded';
import AccountCircle from '@material-ui/icons/AccountCircle';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import {Route, Switch, Redirect } from 'react-router-dom';
import VersionControlContainer from './VersionControlContainer.jsx';
import AddVersionControlContainer from './AddVersionControlContainer.jsx';
import TableDetailContainer from  './TableDetailContainer.jsx'
import ManageAdminContainer from './ManageAdminContainer.jsx'
import {DEV2,DEV3,DEV4,DEV5} from '../Constants.js'
import TableMappingContainer from './TableMappingContainer.jsx';
import EditTableDetailContainer from './EditTableDetailContainer.jsx';
import EditTableMappingContainer from './EditTableMappingContainer.jsx';
import FlywayTableContainer from './FlywayTableContainer.jsx';
import AddTableContainer from './AddTableContainer.jsx'


const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
  },
  appBarGradient:{
    // background: 'linear-gradient(45deg, #8E0E00 30%, #1F1C18 100%)'
    background: 'linear-gradient(45deg, #c31432 30%, #240b36 100%)',
  },
  sideBarGradient:{
    background: 'linear-gradient(45deg, #000000 10%, #434343 90%)',
    color: 'white',
  },
  whiteColor:{
    color: 'white',
    textColor: '#ffffff', // this should work
    primaryTextColor: '#ffffff'
  },
  contentBgColor:{
    background:'linear-gradient(0deg, #434343 30%, #E1E2E1 100%)',
    //background:'white'
  },
  grow: {
    flexGrow: 1,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    height:'4em',
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: 0.0//theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: 0.0,//theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 36,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
    whiteSpace: 'nowrap',
  },
  drawerOpen: {
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: 0.0//theme.transitions.duration.enteringScreen,
    }),
  },
  drawerClose: {
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: 0.0//theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: theme.spacing.unit * 7 + 1,
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing.unit * 9 + 1,
    },
  },
  toolbar: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 3,
  },
});


var sidebarList = ['Tables','Flyways', 'Version Control','Add Version Control', 'Manage Admin', 'Tutorial']
var icons = [<TableIcon/>,<SQLIcon/>,<VersionControlIcon />,<AddVersionControlIcon/>,<AdminIcon />,<TutorialIcon />]

var adminLabel = "";

class HomeContainer extends React.Component {
  constructor(props){
    super(props)
    this.handleListMenu = this.handleListMenu.bind(this)
    
    this.state = {
      env:DEV4,
      envOpen: false,
      profileOpen:false,
      anchorEl: null,
      selected:null,
      username:this.props.username
    };
  }
 

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };
  highlightTabMenu = (index) => {
  
    this.setState({selected:sidebarList[index]})
  }
  handleChange = event => {
    this.setState({ auth: event.target.checked });
  };
  handleListMenu = (key,index) => {
   // event.preventDefault();
      console.log('check key',key,' ',index);//,' check event ',event)
      switch(key){
        case 'Tables':
        this.setState({selected:sidebarList[index]})
          this.props.history.push("/configtable");
          break
        // case 'Edit Table':
        // this.setState({selected:sidebarList[index]})
        //   this.props.history.push("/configtable/edittable");
        //   break
        case 'Flyways':
        this.setState({selected:sidebarList[index]})
          this.props.history.push("/flywaytable")
          break
        case 'Version Control':
        this.setState({selected:sidebarList[index]})
           this.props.history.push("/versioncontrol")
           break
        case 'Add Version Control':
        this.setState({selected:sidebarList[index]})
         this.props.history.push("/versioncontrol/add")
         break
        case 'Manage Admin':
          this.setState({selected:sidebarList[index]})
             this.props.history.push("/manageadmin")
             break
        case 'Tutorial':
          this.setState({selected:sidebarList[index]})
             this.props.history.push("/tutorial")
             break
        default:
        break;
    }
  }

  handleProfileMenu = event => {
    console.log("event current ",event.currentTarget)
    // this.props.history.push("/versioncontrol")
    this.setState({ 
      anchorEl: event.currentTarget,
      profileOpen: true
     });
  };
  handleEnvMenu = event => {
    console.log("event current ",event.currentTarget)
    this.setState({ 
      anchorEl: event.currentTarget,
      envOpen: true
     });
  }
  handleClose = () => {
    this.setState({ 
      anchorEl: null,
      envOpen:false,
      profileOpen:false
    });
  };
 componentDidMount(){
  // ['Tables','Flyways', 'Version Control','Add Version Control', 'Manage Admin', 'Tutorial']
  // [<TableIcon/>,<SQLIcon/>,<VersionControlIcon />,<AddVersionControlIcon/>,<AdminIcon />,<TutorialIcon />]
  
  console.log("user role is here ",this.props.userRole)
  if(this.props.userRole === 0){
    for( var i = 0; i < sidebarList.length; i++){ 

      // if(sidebarList[i] === "Edit Table"){
      //   sidebarList.splice(i, 1); 
      //   icons.splice(i,1)
      // }
      if(sidebarList[i] === "Add Version Control"){
        sidebarList.splice(i, 1); 
        icons.splice(i,1)
      }
      if(sidebarList[i] === "Manage Admin") {
        sidebarList.splice(i, 1); 
        icons.splice(i,1)
      }
      if(sidebarList[i] === "Flyways") {
        sidebarList.splice(i, 1); 
        icons.splice(i,1)
      }
   }

   
   console.log("remaining menu ",sidebarList)
  }
  

   else if (this.props.userRole === 1) {
     adminLabel =  "Admin" //require('../assets/admin.png')
    for( var i = 0; i < sidebarList.length; i++){ 
      if(sidebarList[i] === "Add Version Control"){
        sidebarList.splice(i, 1); 
        icons.splice(i,1)
      }

    
      if(sidebarList[i] === "Manage Admin") {
        sidebarList.splice(i, 1); 
        icons.splice(i,1)
      }
    }
  }
  else if(this.props.userRole === 2){
    // adminIcon = require('../assets/super-admin.png')
    adminLabel =  "Super Admin"
  }

   this.setState({
    loading:false
   })
   //this.props.history.push("/configtable");

 }

  handleLogout = () => {
    this.setState({
        profileOpen:false
    })
      this.props.logout()
  }
  handleClickUserFlywayTable = () => {
    
    this.handleListMenu('Flyways',1)
    this.setState({
      profileOpen:false
    })
  }
  
  handleChangeEnv = (env) => {
   
    this.setState({
      env:env,
      anchorEl: null,
      envOpen:false,
      profileOpen:false,
      selected:'Tables'
    })
    this.props.history.replace("/configtable")
    // this.forceUpdate()
    // this.handleListMenu('Tables',0)
    
    
  }

  render() {
    
    const { classes, theme } = this.props;
    const { anchorEl, selected , env, open , username} = this.state;
    const envOpen = this.state.envOpen
    const profileOpen = this.state.profileOpen
    const isopen = Boolean(anchorEl);
    
    // const { logout } = this.props;


    return (
      
      <div className={classes.root}>
       
        <CssBaseline />
        <AppBar
          position="fixed"
          className={classNames(classes.appBarGradient,classes.appBar, {
            [classes.appBarShift]: open,
          })}
        >
          <Toolbar disableGutters={!open}>
            <IconButton
              color="inherit"
              aria-label="Open drawer"
              onClick={this.handleDrawerOpen}
              className={classNames(classes.menuButton, {
                [classes.hide]: open,
              })}
            >
              <MenuIcon />
            </IconButton>
            <img 
        src={require('../assets/logo.svg')}
        alt="configMonsterLogo"
        // src="../../assets/logo.svg"
        width="30"
        height="30"
        className="d-inline-block align-top"
      />

            {<h1>&nbsp;</h1>}
            <Typography variant="h6" color="inherit" className={classes.grow}>
            Config Monster
            </Typography>
            {/* <img 
        src={adminIcon}
        alt="adminLogo"
        // src="../../assets/logo.svg"
        // width="110"
        // height="40"
        
        className="d-inline-block align-top"
      /> */}
   
       <Button
              variant="contained" 
              style={{backgroundColor:"#f9a825",fontWeight:"bold",pointerEvents:"none" }}
              className="d-inline-block align-top"
          >

            {adminLabel}
        </Button>
      <label>&nbsp;</label>
              <div>
              <Button
              variant="contained" 
              color="secondary"
            aria-owns={isopen ? 'menu-list-grow' : undefined}
            aria-haspopup="true"
            onClick={this.handleEnvMenu}
          >
            {env}
          </Button>
          <Menu
          id="simple-menu"
          anchorEl={anchorEl}
          open={envOpen}
          onClose={this.handleClose}
        >
          <MenuItem onClick={this.handleChangeEnv.bind(this,DEV2)}>{DEV2}</MenuItem>
          <MenuItem onClick={this.handleChangeEnv.bind(this,DEV3)}>{DEV3}</MenuItem>
          <MenuItem onClick={this.handleChangeEnv.bind(this,DEV4)}>{DEV4}</MenuItem>
          <MenuItem onClick={this.handleChangeEnv.bind(this,DEV5)}>{DEV5}</MenuItem>
        </Menu>
                <IconButton
                  aria-owns={isopen ? 'menu-appbar' : undefined}
                  aria-haspopup="true"
                  onClick={this.handleProfileMenu}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton>

                <Menu
                  id="menu-appbar"
                  anchorEl={anchorEl}
                  anchorOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  transformOrigin={{
                    vertical: 'top',
                    horizontal: 'right',
                  }}
                  open={profileOpen}
                  onClose={this.handleClose}
                >
                {this.props.userRole !== 0 ? <MenuItem onClick={this.handleClickUserFlywayTable}>My Flyways Table</MenuItem> :<div/>}
                  <MenuItem onClick={this.handleLogout}>Log Out</MenuItem>
                  
                  {/* <MenuItem onClick={this.handleClose}>My account</MenuItem> */}
                </Menu>
                
              </div>
            
          </Toolbar>
        </AppBar>
        <Drawer
          variant="permanent"
          className={classNames(classes.drawer, {
            [classes.drawerOpen]: open,
            [classes.drawerClose]: !open,
          })}
          classes={{
            paper: classNames(classes.sideBarGradient,{
              [classes.drawerOpen]: open,
              [classes.drawerClose]: !open,
            }),
          }}
          open={open}
        >
          <div className={classes.toolbar}>
            <IconButton className={classes.whiteColor} onClick={this.handleDrawerClose}>
              {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
            </IconButton>
          </div>
          <Divider />
          <List>
            {sidebarList.map((text, index) => (
              <ListItem button 
              key={text} 
              id = {text} 
              selected={selected === sidebarList[index]}
              onClick={this.handleListMenu.bind(this,text,index)}>
                <ListItemIcon className={classes.whiteColor} >{icons[index]}</ListItemIcon>
                <ListItemText primary={<Typography variant="subtitle1" style={{ color: '#ffffff' }}>{text}</Typography>}/>
              </ListItem>
            ))}
          </List>
        </Drawer>
        {/* <main style={{'height':'1000vh'}}> */}
        {/* style={{'height':'500vh'}}  */}
        <main className={classNames(classes.content)}>
        {/* <main style={{'height':'100vh'}} className={classNames(classes.content,classes.contentBgColor)}> */}
          <div className={classes.toolbar} />
            <Switch>
            <Route exact path="/" render={(props) => (<TableMappingContainer {...props} highlightTabMenu={this.highlightTabMenu}  env={env} />)} />
            <Route exact path="/configtable" render={(props) => (<TableMappingContainer {...props} highlightTabMenu={this.highlightTabMenu}  env={env} userRole={this.props.userRole} />)} />
            {/* <Route exact path="/configtable/edittable" render={(props) => (<AddTableContainer {...props} highlightTabMenu={this.highlightTabMenu}  env={env} />)} /> */}
            <Route path="/configtable/detail" render={(props) => (<TableDetailContainer {...props} highlightTabMenu={this.highlightTabMenu}  env={env} userRole={this.props.userRole} />)} />
            <Route path="/flywaytable" render={(props) => (<FlywayTableContainer {...props}  highlightTabMenu={this.highlightTabMenu} env={env} username={username}/>)} />
            <Route exact path="/versioncontrol" render={(props) => (<VersionControlContainer {...props}  highlightTabMenu={this.highlightTabMenu} env={env}/>)} />
            <Route path="/manageadmin" render={(props) => (<ManageAdminContainer {...props}  highlightTabMenu={this.highlightTabMenu} env={env} />)} />
            <Route path="/configtable/edittabledata" render={(props) => (<EditTableDetailContainer {...props}  highlightTabMenu={this.highlightTabMenu} env={env} username={username}/>)} />
            <Route path="/configtable/edittablemapping" render={(props) => (<EditTableMappingContainer {...props}  highlightTabMenu={this.highlightTabMenu} env={env} />)} />
            <Route path="/versioncontrol/add" render={(props) => (<AddVersionControlContainer {...props}  highlightTabMenu={this.highlightTabMenu} env={env} />)} />
            {/* <Route render={(props) => (<TableMappingContainer {...props} highlightTabMenu={this.highlightTabMenu}  env={env} />)}/> */}
            </Switch>
        </main>
      </div>
    );
  }
}

HomeContainer.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(HomeContainer);

