class EditTableMappingContainer extends React.PureComponent {
  
  constructor(props) {
    super(props);

    this.state = {
      tableName: "",
      columns: [],
      rows: [],
      loading: false,
      pageSizes: [20,50,100,0]
      // rows: generateRows({
      //   columnValues: { id: ({ index }) => index, ...defaultColumnValues },
      //   length: 8,
      // }),
    };

    this.commitChanges = this.commitChanges.bind(this);
  }
  getColumnName = (obj) => {
    let column = JSON.parse(JSON.stringify(obj))
    return column.name
  }
  queryTable = () => {


    this.setState({
      loading: true
    })
    fetch(QUERY_TABLE_MICROSERVICE_API + '?env=' + this.props.env, {
      method: "GET"
    })
      .then(response => {
        console.log("response status ", response.status === 200)
        if (response.status === 200) {
          return response.json()
        }
        else {
          console.log("cannot get data")

          throw new Error(response.status);
        }
      }).then(data => {
        console.log("still enter data? ")
        var column = []
        for (var i = 0; i < data.column.length; i++) {
          column.push({
            name: data.column[i],
            label: data.column[i],
            options: {
              filter: true,
            }
          });
        }
        if(this.isMount){
        this.setState({
          rows: data.data,
          columns: column,
          loading: false,
        })
      }
      }).catch((error) => {
        console.log('error: ' + error);
        this.setState({
          loading: false
        })
      });
      
  }


  componentDidMount() {
    this.isMount = true
    this.props.highlightTabMenu(TABLE_MAPPING_INDEX)
    this.queryTable()
  }
  componentWillUnmount(){
    this.isMount = false
  }
  
  addData = (data) => {
    console.log("rows is rows ",data[0].id)
    
  }
  updateData = () => {

  }
  deleteData = () => {

  }

  // changeAddedRows(addedRows) {

  //   const initialized = addedRows.map(row => (Object.keys(row).length ? row : { city: 'Tokio' }));
  //   console.log("added new row",initialized)
  //   // this.setState({ addedRows: initialized });
  // }

  // changeEditingRowIds(editingRowIds) {
  //   console.log("editing row id",editingRowIds)
  //   // this.setState({ editingRowIds });
  // }

  // changeRowChanges(rowChanges) {
  //   console.log("change row ",rowChanges)
  //   // this.setState({ rowChanges });
  // }

  commitChanges({ added, changed, deleted }) {
    console.log("commit changes ", added, ' ', changed, ' ', deleted)
    let { rows } = this.state;
    if (added) {
      const startingAddedId = rows.length > 0 ? rows[rows.length - 1].id + 1 : 0;
      rows = [
        ...rows,
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
      ];
      this.addData(added)

    }

    if (changed) {
      rows = rows.map(row => (changed[row.id] ? { ...row, ...changed[row.id] } : row));
    }

    if (deleted) {
      const deletedSet = new Set(deleted);
      rows = rows.filter(row => !deletedSet.has(row.id));
    }

    this.setState({ rows });
  }

  render() {

    const { rows, columns } = this.state;
   


    return (
      <LoadingOverlay
      active={this.state.loading}
      spinner
      >
      <MuiThemeProvider theme={theme}>
      <span>
        </span>
        <Paper>
          <Grid
            rows={rows}
            columns={columns}
          // getRowId={getRowId}
          >
          <SearchState/>
          <FilteringState defaultFilters={[]} />
          <IntegratedFiltering />
            <PagingState
              defaultCurrentPage={0}
              defaultPageSize={0}
            />
            <IntegratedPaging />
            <SortingState /> 
          <IntegratedSorting />
            <Table />

                       
         
            <VirtualTable
              height="auto"
            />
            <TableHeaderRow showSortingControls />
            <PagingPanel
              pageSizes={this.state.pageSizes}
            />
            <EditingState
              // onEditingRowIdsChange={this.changeEditingRowIds}
              // onAddedRowsChange={this.changeAddedRows}
              // onRowChangesChange={this.changeRowChanges}
              onCommitChanges={this.commitChanges}
            />

           

            <TableEditRow />
            <TableEditColumn
              showAddCommand
              showEditCommand
              showDeleteCommand
              commandComponent={Command}
            />
             <Toolbar />
          <SearchPanel />
          <TableFilterRow />
          </Grid>
        </Paper>
      </MuiThemeProvider>
      </LoadingOverlay>

    );


  }
}

