import React, { Component } from 'react';
import MUIDataTable from "mui-datatables";
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import pink from 'material-ui/colors/pink';
import queryString from 'query-string'
import { EXPORT_DATA_API,QUERY_TABLE_DETAIL_API,TABLE_MAPPING_INDEX } from '../Constants.js'
import LoadingOverlay from 'react-loading-overlay';
import CustomToolbar from "../components/CustomToolbar.jsx";
import Button from '@material-ui/core/Button';
import DownloadIcon from '@material-ui/icons/CloudDownloadRounded';
import fileSaver from 'file-saver'
import axios from 'axios'
var tableId = 0;
var isMount = true
class TableDetailContainer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            title: 'Config Table',
            column:[],
            data: [],
            row: 20,
            loading:false
        }


    }
    exportData = () => {
        axios.post(EXPORT_DATA_API,this.state.data, { responseType: 'arraybuffer' })
    .then((response) => {
       var blob = new Blob([response.data], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
     fileSaver.saveAs(blob, this.state.title+'.xlsx');
    });
    }
   
    queryTable = (id) => {
        this.setState({
            loading:true
        })
        fetch(QUERY_TABLE_DETAIL_API + '?env=' + this.props.env+'&id='+id, {
            method: "GET"
        })
            .then(response => {
                console.log("response status ",response.status === 200)
                if (response.status === 200) {
                    return response.json()
                }
                else {
                    console.log("cannot get data")
                    
                    throw new Error(response.status);
                }
            }).then(data => {
                console.log("still enter data?")
               var column = []
                for (var i = 0; i < data.column.length; i++) {
                    var sort=null
                    if(i==0){sort='asc'}
                    column.push({
                        name: data.column[i],
                        label: data.column[i] + " [" + data.type[i] +"]",
                        options: {
                          filter: true,
                          sortDirection:sort
                         }
                    });
                }
                if(isMount){
                   
                this.setState({
                    data: data.data,
                    column:column,
                    title:data.tableName,
                    loading:false
                })
            }
            }).catch((error) => {
                console.log('error: ' + error);
                this.setState({
                    loading:false
                })
              });
    }


    componentDidMount() {
        isMount = true
        this.props.highlightTabMenu(TABLE_MAPPING_INDEX)
        const values = queryString.parse(this.props.location.search)
        tableId = values.id
        this.queryTable(tableId)

    }
    componentWillUnmount(){
       isMount = false
    }

    handleEdit = () => {
        const values = queryString.parse(this.props.location.search)
        this.props.history.push("/configtable/edittabledata?id="+values.id)
    }


    getMuiTheme = () => createMuiTheme({
        palette: {
            //type: "dark",
            primary: pink
        },

        typography: {
            useNextVariants: true,
            fontSize: 18 // to support newer version -> 
        },
        overrides: {
            MUIDataTableBodyCell: {
                root: {
                    
                }
            },
            MUIDataTableSelectCell: {
                checked: { color: "#FF0266 !important" }
            }

        }
    })

    render() {

        const options = {
            //    filterType: 'checkbox',
            rowsPerPage: this.state.row,
            rowsPerPageOptions: [20, 50, 100, this.state.data.length],
            selectableRows: false,
            print: false,
         download:false,
            filterType: 'textField',
            responsive:'scroll',
            onChangeRowsPerPage: (row) => {
                this.setState({
                    row:row
                })
            },

            customToolbar: () => {
                if(this.props.userRole < 1){
                    return null
                }
                else {
                    return <CustomToolbar handleEdit={this.handleEdit}/>;
                }
                
              },

        };

        return (
            <div>
                   <LoadingOverlay
      active={this.state.loading}
      spinner
      >
                <MuiThemeProvider theme={this.getMuiTheme()}>
                <Button variant="contained" onClick={this.exportData} color="secondary">
                    <DownloadIcon/>
                </Button>
    
                    <MUIDataTable
                        title={this.state.title}
                        data={this.state.data}

                        // data={!this.state.data.length === 0 ? []
                        //     :
                        //     this.state.data.map(item => {
                        //         return [
                        //             item.table_name,
                        //             item.microservice
                        //         ]
                        //     })
                        // }
                        

                    
                    columns={this.state.column}
                    options={options}

                    />
    </MuiThemeProvider>
    </LoadingOverlay>
            </div>

        )
    }
}
export default TableDetailContainer;
