

import * as React from 'react';
import Paper from '@material-ui/core/Paper';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import { EditingState } from '@devexpress/dx-react-grid';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import SaveIcon from '@material-ui/icons/Save';
import CancelIcon from '@material-ui/icons/Cancel';
import { createMuiTheme, MuiThemeProvider } from '@material-ui/core/styles';
import LoadingOverlay from 'react-loading-overlay';
import SweetAlert from 'sweetalert2-react';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';

import DialogTitle from '@material-ui/core/DialogTitle';
import pink from 'material-ui/colors/pink';
import blue from 'material-ui/colors/blue';
import FormHelperText from '@material-ui/core/FormHelperText';
import {
  SortingState,
  IntegratedSorting,
  PagingState,
  IntegratedPaging,
  SearchState,
  IntegratedFiltering,
  FilteringState,
  SelectionState
} from '@devexpress/dx-react-grid';
import {
  Grid,
  Table,
  TableHeaderRow,
  TableSelection,
  TableEditRow,
  TableEditColumn,
  PagingPanel,
  Toolbar,
  SearchPanel,
  VirtualTable,
  ColumnChooser,
  TableColumnVisibility,
  TableFilterRow,
} from '@devexpress/dx-react-grid-material-ui';
import queryString from 'query-string'
import { QUERY_TABLE_DETAIL_API, TABLE_MAPPING_INDEX,SAVE_TABLE_DETAIL_API } from '../Constants.js'


const getRowId = row => row.rowNumber;
var tableId = 0;
const theme = createMuiTheme({
  typography: {
    useNextVariants: true,
    fontSize: 18 // to support newer version -> 
  },
  palette: {
    //type: "dark",
    primary:blue,
    secondary:pink
  },
  

});

const AddButton = ({ onExecute }) => (
  <div style={{ textAlign: 'center' }}>
    <Button
      color="secondary"
      onClick={onExecute}
      title="Create new row"
    >
      New
    </Button>
  </div>
);

const EditButton = ({ onExecute }) => (
  <IconButton onClick={onExecute} title="Edit row">
    <EditIcon />
  </IconButton>
);

const DeleteButton = ({ onExecute }) => (
  <IconButton
    onClick={() => {
      // eslint-disable-next-line
      if (window.confirm('Are you sure you want to delete this row?')) {
        onExecute();
      }
    }}
    title="Delete row"
  >
    <DeleteIcon />
  </IconButton>
);

const CommitButton = ({ onExecute }) => (
  <IconButton onClick={onExecute} title="Save changes">
    <SaveIcon />
  </IconButton>
);

const CancelButton = ({ onExecute }) => (
  <IconButton color="secondary" onClick={onExecute} title="Cancel changes">
    <CancelIcon />
  </IconButton>
);

const commandComponents = {
  add: AddButton,
  edit: EditButton,
  delete: DeleteButton,
  commit: CommitButton,
  cancel: CancelButton,
};
const Command = ({ id, onExecute }) => {
  const CommandButton = commandComponents[id];
  return (
    <CommandButton
      onExecute={onExecute}
    />
  );
};

// const HeaderCellBase = ({ classes, className, ...restProps }) => (
//   <TableHeaderRow.Cell
//     {...restProps}
//     className={`${classes.text} ${className}`}
//   />
// );
// const HeaderCell = withStyles(headerCellStyles, { name: 'HeaderCellBase' })(HeaderCellBase);

// const getRowId = row => row.id;

var existingTableData;

class EditTableDetailContainer extends React.PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      tableName: "",
      columns: [],
      rows: [],
      loading: false,
      selections:[],
      pageSizes: [10, 20, 50, 0],
      showAlert:false,
      alertType:"",
      alertTitle:"",
      alertMessage:"",
      openDialog:false,
      description:"",
      devTask:"",
      devTaskError:false,
      descriptionError:false
      // rows: generateRows({
      //   columnValues: { id: ({ index }) => index, ...defaultColumnValues },
      //   length: 8,
      // }),
    };
    this.updateData = this.updateData.bind(this)

    this.commitChanges = this.commitChanges.bind(this);
    this.totalBeforeData = []
    this.totalAfterData = []
    // this.totalUpdateData = []
  }
  getColumnName = (obj) => {
    let column = JSON.parse(JSON.stringify(obj))
  
    return column.name
  }
  queryTable = (id) => {

    this.setState({
      loading: true
    })
    fetch(QUERY_TABLE_DETAIL_API + '?env=' + this.props.env + '&id=' + id, {
      method: "GET"
    })
      .then(response => {
        if (response.status === 200) {
          return response.json()
        }
        else {

          throw new Error(response.statusText);
        }
      }).then(data => {
        var column = []
        for (var i = 0; i < data.column.length; i++) {
          column.push({
            name: data.column[i],
            label: data.column[i],
            options: {
              filter: true,
            }
          });
        }
        existingTableData = data.data
        if(this.isMount){
        this.setState({
          rows: data.data,
          columns: column,
          loading: false,
          tableName:data.tableName
        })
      }
      }).catch((error) => {

        this.setState({
          loading: false,
          showAlert:true,
          alertType:"error",
          alertTitle:"Error!",
          alertMessage:"Error occurred, "+error.message,
        })
      });
      
  }


  componentDidMount() {
    this.isMount = true
    this.props.highlightTabMenu(TABLE_MAPPING_INDEX)
    const values = queryString.parse(this.props.location.search)
    tableId = values.id
    this.queryTable(tableId)
  }
  componentWillUnmount(){
    this.isMount = false
  }

  addData = (data) => {
    var before = {}
    var after = {}
    Object.keys(data[0]).forEach(function(key) {
      after[key] = data[0][key]
    });
    this.totalBeforeData.push(before)
    this.totalAfterData.push(after)
    
  }

  updateData = (previous,data) => {
    // delete previous.rowNumber
    var before = previous
    var after = {}
    Object.keys(data).forEach(function(key) {
      after = data[key]
      // before = existingTableData[key]
    });
    this.totalBeforeData.push(before)
    this.totalAfterData.push(after)
    console.log("check before update data ",this.totalBeforeData)
    console.log("check after update data ",this.totalAfterData)
    
  }

  deleteData = (previous,data) => {
    delete previous.rowNumber
    var before = previous
    var after = {}
    // const rowData = this.state.rows
    // Object.keys(this.state.rows[data[0]]).forEach(function(key) {
    //   before[key] = rowData[data[0]][key]
    // });
    this.totalBeforeData.push(before)
    this.totalAfterData.push(after)
    console.log("check delete before data ",this.totalBeforeData)
    console.log("check delete after data ",this.totalAfterData)
  }


  handleSave = () => {

    if(this.totalBeforeData.length !== 0 && this.totalAfterData.length !== 0){
    this.setState({ openDialog: true });
    }
  }
  handleClose = () => {
    this.setState({ openDialog: false });
  }
  handleChange = value => event => {
    this.setState({ [value]: event.target.value });
  };

  saveAllData = () => {
    let descriptionErrorFlag = false
    let devTaskErrorFlag = false
    if(this.state.description === "" || this.state.description === " "){
      descriptionErrorFlag = true
     
    }
    if(this.state.devTask === "" || this.state.devTask === " "){
      devTaskErrorFlag = true
    }
    if (descriptionErrorFlag || devTaskErrorFlag){
      this.setState({
        descriptionError:descriptionErrorFlag,
        devTaskError:devTaskErrorFlag
      })
      return
    }
   
    this.setState({
      loading:true,
      openDialog:false,
      descriptionError:false
    })
    const values = queryString.parse(this.props.location.search)
    const id = values.id
    fetch(SAVE_TABLE_DETAIL_API, {
      method: "POST",
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        "id":id,
        "env":this.props.env,
        "username":this.props.username,
        "before": this.totalBeforeData,
        "after":this.totalAfterData,
        "description":this.state.description,
        "devTask":this.state.devTask
     })
    })
      .then(response => {
       
        if (response.status === 200) {
          this.setState({
            loading:false,
            showAlert:true,
            alertType:"success",
            alertTitle:"Success!",
            alertMessage:"Successfully Save Table Data."
          })
          this.props.history.push("/configtable/detail?id="+tableId)
          return response.json()
        }
        else {
         
          throw new Error(response.statusText);
        }
      }).catch((error) => {
        this.setState({
          loading:false,
          showAlert:true,
          alertType:"success",
          alertTitle:"Success!",
          alertMessage:"Cannot Save Table Data, Please try again. "+error.message
         
        })
        this.props.history.push("/configtable/detail?id="+tableId)
      });
  }
  
  
  isEmpty = (obj) => {
    return !Object.keys(obj).length > 0;
  }

  commitChanges({ added, changed, deleted }) {
   
   
    let { rows } = this.state;

    var changeIndex;
    if (added) {
      if(Object.keys(added["0"]).length > 0){

      const startingAddedId = rows.length > 0 ? rows[rows.length - 1].rowNumber + 1 : 0;
      rows = [
        ...rows,
        ...added.map((row, index) => ({
          rowNumber: startingAddedId + index,
          ...row,
        })),
      ];
    
      this.addData(added)
    }
    }
    if (changed) {
      // rows = rows.map(row => (changed[row.rowNumber] ? { ...row, ...changed[row.rowNumber] } : row));
      rows.forEach(function(row){
        
        if(changed[row.rowNumber]){
          
          changeIndex = row.rowNumber
        }
        
      })
      rows = rows.map(row => (changed[row.rowNumber] ? { ...row, ...changed[row.rowNumber] } : row));
      const before = this.state.rows[changeIndex]
      this.updateData(before,changed)
    }
    if (deleted) {
      const deletedSet = new Set(deleted);
      
      rows = rows.filter(row => !deletedSet.has(row.rowNumber));
      const before = this.state.rows[deleted[0]]
      this.deleteData(before,deleted)
    }

    this.setState({ rows });
    console.log("checking rows eiei ",rows)
  }
  getRowIndex(row, columnName) {
    return columnName === "index"
      ? this.state.rows.indexOf(row)
      : row[columnName];
  }

  render() {

    const { rows, columns } = this.state;
   

    return (
      <div>
      { columns.length!==0?
      <LoadingOverlay
      active={this.state.loading}
      spinner
      >
       
      <MuiThemeProvider theme={theme}>
      <SweetAlert
        show={this.state.showAlert}
        title={this.state.alertTitle}
        text={this.state.alertMessage}
        type={this.state.alertType}
        onOutsideClick={() => this.setState({ showAlert: false })}
        onConfirm={() => this.setState({ showAlert: false })}
      />
      <Dialog
          open={this.state.openDialog}
          onClose={this.handleClose}
          aria-labelledby="form-dialog-title"
          maxWidth="sm"
          fullWidth = {true}
        >
          <DialogTitle id="form-dialog-title">Submit your description to save this config table</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              required
            error={this.state.descriptionError}
              margin="dense"
              id="description"
              label="Description"
              type="text"
              value={this.state.description}
              multiline
              rowsMax="10"
          onChange={this.handleChange('description')}
              fullWidth
            />
            {this.state.descriptionError && <FormHelperText>This is required!</FormHelperText>}
           
          </DialogContent>
          <DialogContent>
            <TextField
               required
               error={this.state.devTaskError}
              margin="dense"
              id="devTask"
              label="Dev Task number"
              type="text"

              value={this.state.devTask}
          onChange={this.handleChange('devTask')}
              fullWidth
            />
             {this.state.devTaskError && <FormHelperText>This is required!</FormHelperText>}
          </DialogContent>
          <DialogActions>
            <Button onClick={this.handleClose} color="primary">
              Cancel
            </Button>
            <Button variant="contained" onClick={this.saveAllData} color="secondary">
              Save
            </Button>
          </DialogActions>
        </Dialog>
      <span>
          <h2>
          {this.state.tableName !== "" ? this.state.tableName+" - Edit" : ""}
          </h2>
        
        </span>
        <Button variant="contained" color="secondary" onClick={this.handleSave}>
        Save
      </Button>
      <br/>
        <Paper>

          <Grid
            rows={rows}
            columns={columns}
            getRowId={getRowId}
          >
          <SearchState/>
          <FilteringState defaultFilters={[]} />
          <IntegratedFiltering />
            <PagingState
              defaultCurrentPage={0}
              defaultPageSize={0}
            />
            <IntegratedPaging />
            <SortingState 

             /> 
            {columns.length!==0?
               <SortingState 
               defaultSorting={[{ columnName: columns[0].name, direction: 'desc' }]}
             /> 
             :
             <SortingState/>
             }
             
           
          <IntegratedSorting />
            <Table/>
            <VirtualTable
              height="auto"
            />
            <TableHeaderRow showSortingControls />
            
           
            <PagingPanel
              pageSizes={this.state.pageSizes}
            />
            <EditingState

              // onAddedRowsChange={this.changeAddedRows}
              // onRowChangesChange={this.changeRowChanges}
              onCommitChanges={this.commitChanges}
              onEditingRowIdsChange={this.changeEditingRowIds}
          />
          

           

            <TableEditRow />
            <TableEditColumn
              showAddCommand
              showEditCommand
              showDeleteCommand
              commandComponent={Command}
            />
             <Toolbar />
          <SearchPanel />
         
          <TableFilterRow />
          </Grid>
         
        </Paper>
      </MuiThemeProvider>
      </LoadingOverlay>
      :
      <div/>
      }
      </div>
    );


  }
}

export default EditTableDetailContainer