import React, { Component } from 'react';
import { Route } from 'react-router-dom';
import './App.css';
import HomeContainer from './containers/HomeContainer.jsx';
import Keycloak from 'keycloak-js';
import {QUERY_USER_ROLE_API,DEV2} from './Constants.js'


class App extends Component {
  

  constructor(props) {
    super(props);
    this.state = { keycloak: null, authenticated: false};
  }
  componentDidMount() {
    // const keycloak = Keycloak({ // -> Test
      
    //   "realm": "Theme-Testing",
    //   "url": "https://keycloak.devops.easy2easiest.com/auth",
    //   "ssl-required": "external",
    //   "clientId": "audit-monster",
    //   "public-client": true,
    //   "confidential-port": 0
      
    // })
    const keycloak = Keycloak({
      "realm": "master",
  "url": "https://keycloak.devops.easy2easiest.com/auth",
  "ssl-required": "external",
  "clientId": "configmonster",
  "public-client": true,
  "confidential-port": 0
    
    })
    // const keycloak = Keycloak({ --> Actual 
    //     "realm": "master",
    //     "url": "https://keycloak.devops.easy2easiest.com/auth",
    //     "ssl-required": "external",
    //     "resource": "auditmonster-localhost",
    //     "clientId": "auditmonster-localhost",
    //     "public-client": true,
    //     "confidential-port": 0
    //    });

       console.log('keycloak : ',keycloak);
       

    keycloak.init({onLoad: 'login-required'}).success(authenticated => {
      
      keycloak.loadUserProfile().success((profile) => {
        fetch(QUERY_USER_ROLE_API, {
          method: "POST",
          
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        "username": profile.username,
     })
      })
          .then(response => {
              console.log("response status ",response.status === 200)
              if (response.status === 200) {
                  return response.json()
              }
              
              else {
                  console.log("cannot get data")
                  return {"role_config":0}
                  // throw new Error(response.status);
              }
          }).then(data => {
            console.log("check role config of user ",data[0])
            this.setState({
              user: profile,
              userRole:data[0].role_config,
              keycloak: keycloak,
              authenticated: authenticated
            });
           
          }).catch((error) => {
              console.log('error: ' + error);
              this.setState({
                  loading:false
              })
            });
        console.log("check user profile ",profile)
       
      }).error((err) => {
        console.log('Failed to load User Profile : ', err);
      });

     })
  }
  logout = () => {
    this.state.keycloak.logout({'clientId':'auditmonster-localhost'});
  }
  render() {

    return (
        <div  className="container">
          { this.state.authenticated ?
          <Route  render={(props) => (<HomeContainer {...props} logout={this.logout} userRole={this.state.userRole} username={this.state.user.username} hello="hello"/>)} />:<div></div>
          }
        </div>
    );
  }
}

export default App;
